.result {
    width: 95%;
    max-height: 300px;

    overflow-y: auto;
    background: url('../images/Fondo-resultados.png') center;
    background-size: contain;
    background-repeat: repeat-y;

    border-radius: 1rem;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-template-rows: repeat(3, 6rem);
    grid-auto-rows: 6rem;
    row-gap: .5rem;
    justify-items: center;
    padding-top: 1rem;
    box-sizing: border-box;
}

.result__item {
    width: 95%;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.219);
    border-radius: .5rem;
    background-color: #fff;
    justify-content: space-around;
    align-items: center;
    font-size: 1rem;
    line-height: 0;
    transition: 200ms;
    
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 3rem 3rem;
}
.result__item:hover{
    outline: 3px solid #bbb;
    cursor: pointer;
    transform: scale(1.02);
}
.operator{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}