/*ESTILO HEADER---------------------------------------*/
.header{
    display: flex;
    align-items: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    background: url('../images/Fondo-de-encabezado.jpg');
    background-size: cover;
    background-position: center;
    color: #fff;
}
.header__title{
    flex-grow: 4;
    width: 100px;
    max-height: 10rem;
    display: flex;
    text-shadow: 2px 2px 3px #000;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 3.5rem;
    align-items: center;
    margin-left: 1rem;
}

.header__subtitle-sena{
    font-size: 3rem;
    text-shadow: 2px 2px 3px #000;
    text-decoration: overline;
    text-align: center;
    align-self: flex-end;
    /* background-color: red; */
}
/*ICONOS - LOGOS*/
.header__icon-sena{
    flex-grow: 0;
    display: flex;
    flex-direction: column;
    list-style: none;
    padding: 0;
    margin: 0;
    max-width: fit-content;
}
.header__sena-item{
    display: flex;
    justify-content: center;
    line-height: 0;
    width: 100%;
}

.header__icon{
    width: 13%;
    height: 13%;
    margin-right: 1rem;
    filter: drop-shadow(0px 0px 2px #000000a1) ;
}

.header__icon-sena{
    width: 22%;
    height: 22%;
    filter: drop-shadow(0px 0px 5px #000);

}
/*ESTILO NAV---------------------------------------*/
.navegation{
    background-color: #273564;
    padding: 0;
    height: 4rem;
    position: sticky;
    top: 0;
    z-index: 9;
    box-shadow: 0 3px 10px 3px rgba(0, 0, 0, 0.308);
}
.navegation__options{
    display: flex;
    justify-content: space-between; 
    height: 100%;
    font-size: 1.3rem;
    font-family: 'Josefin Sans', sans-serif;
    list-style: none;
    align-items: center;
    margin: 0;
}
.navegation__anchor{
    margin: 0;
    text-decoration: none;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    /* padding: 1rem 3rem 1rem 3rem; */
    width: 100%;
    height: 100%;
    color: #fff;
    border-radius: .8rem;
}
.navegation__item{
    height: 92%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1 ;
    transition: 90ms;
}
/* .navegation__item:hover{
    background-color: #9498d8;
} */
.navegation__anchor:hover{
    background-color: #9498d8;
}
.active{
    font-weight: 600;
}
.active::before,.active::after{
    content: "";
    position: relative;
    height: 1rem;
    width: 1rem;
    background-color: #9498d8;
    border-radius: 50%;
    margin: 0 .2rem 0 .2rem;
}
